import React from "react";
import { BrowserProvider } from "ethers"; // Import BrowserProvider chnh xc t ethers

const ConnectWallet = ({ setAddress, setProvider }) => {
  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const provider = new BrowserProvider(window.ethereum); // S dng BrowserProvider
        await provider.send("eth_requestAccounts", []); // Yu cu ngi dng kt ni ti khon
        const signer = await provider.getSigner(); // Ly signer t provider
        const account = await signer.getAddress(); // Ly a ch v
        setAddress(account); // Cp nht a ch v trong state
        setProvider(provider); // Cp nht provider trong state
        alert(`Connected: ${account}`);
      } catch (err) {
        console.error("Connection error:", err);
        alert("Could not connect to MetaMask. Please try again.");
      }
    } else {
      alert("MetaMask is not installed! Please install MetaMask to continue.");
    }
  };

  return (
    <div style={{ textAlign: "center", margin: "20px" }}>
      <button
        onClick={connectWallet}
        style={{
          padding: "10px 20px",
          cursor: "pointer",
          backgroundColor: "#007bff",
          color: "#fff",
          border: "none",
          borderRadius: "5px",
        }}
      >
        Connect Wallet
      </button>
    </div>
  );
};

export default ConnectWallet;
